<template>
  <b-modal @ok="handleOk" centered size="lg"
          ref="modalTemplate"
          id="modalTemplate"
          :title="id !== 0 ? 'Editar plantilla' : 'Nueva plantilla'"
          no-close-on-backdrop
          no-close-on-esc
          lazy
          hide-footer
          :header-bg-variant="headerBgVariant"
          :header-text-variant="headerTextVariant"
          @show="getData"
          @hidden="reset">
    <b-form @submit.stop.prevent="handleSubmit">
      <b-row>
        <b-col>
          <b-form-group id="lbname"
                        label="Nombre de la Plantilla"
                        label-for="txname">
            <b-form-input
                      placeholder="Introduzca el nombre de la platilla..."
                      id="txname"
                      v-model="name"
                      required>
            </b-form-input>
          </b-form-group>
        </b-col>
      </b-row>

      <b-row>
        <b-col>
          <b-form-group label="Tipo de plantilla">
            <b-form-radio-group id="type" v-model="type" name="type" required>
              <b-form-radio value="0">SMS</b-form-radio>
              <b-form-radio value="1">Email</b-form-radio>
            </b-form-radio-group>
          </b-form-group>
        </b-col>
      </b-row>

      <b-row>
        <b-col>
          <b-form-group id="lbbody"
                        label="Plantilla: (Usa @ para insertar campos inteligentes)"
                        label-for="txbody">
          <b-form-textarea id="txbody"
                     v-model="body"
                     placeholder="Introduzca el contenido de la plantilla..."
                     :rows="5"
                     :max-rows="6"
                     required maxlength="500">
          </b-form-textarea>
          </b-form-group>
          <h6 class="label-count">{{ body.length + '/500'}}</h6>
        </b-col>
      </b-row>

      <b-container>
        <b-row align-h="end">
          <b-button class="mr-2" type="button" v-on:click="onCancel" variant="dark">Cancelar</b-button>
          <b-button class="mr-0" type="submit" variant="primary">Aceptar</b-button>
        </b-row>
      </b-container>
    </b-form>
  </b-modal>
</template>

<script>
import { mapGetters } from 'vuex'
import select from 'vue-select'

export default {
  components: {
    'v-select': select
  },
  computed: {
    ...mapGetters({
      workgroupId: 'getWorkgroupId',
      id: 'template/getTemplateById'
    })
  },
  data () {
    return {
      headerBgVariant: 'success',
      headerTextVariant: 'light',
      name: '',
      body: '',
      type: ''
    }
  },
  methods: {
    handleOk (evt) {
      evt.preventDefault()
    },
    getData () {
      if (this.id !== 0) {
        const template = this.$store.state.template.template

        this.name = template.name
        this.body = template.body
        this.type = template.type
      }
    },
    reset () {
      this.name = ''
      this.body = ''
      this.type = '0'
      this.$store.commit('template/setTemplateId', 0)
    },

    formPrepare: function () {
      const form = {}

      if (this.id !== 0) {
        this.$set(form, 'id', this.id)
      }

      this.$set(form, 'name', this.name)
      this.$set(form, 'type', this.type)
      this.$set(form, 'id_workgroup', this.workgroupId)
      this.$set(form, 'body', this.body)

      return JSON.stringify(form)
    },
    handleSubmit () {
      this.$store.dispatch('setLoading', true)
      const JsonData = this.formPrepare()
      let promise = null

      if (this.id === 0) {
        promise = this.$store.dispatch('template/setTemplate', JsonData)
      } else {
        promise = this.$store.dispatch('template/updateTemplate', JsonData)
      }

      promise.then(data => {
        this.$root.$emit('bv::refresh::table', 'TBTemplates')
        this.$root.$emit('bv::hide::modal', 'modalTemplate')
        this.$toaster.success('Operación exitosa.')
        this.$store.dispatch('setLoading', false)
      }).catch(() => { this.$store.dispatch('setLoading', false) })
    },
    onCancel () {
      this.$root.$emit('bv::hide::modal', 'modalTemplate')
    }
  },
  mounted: function () {
  }
}
</script>



