import Vue from 'vue'
import VueResource from 'vue-resource'
import store from '../vuex/index'

Vue.use(VueResource)

const baseUrl = require('./config.js').ConectionUrlBase

const selectService = {
  getRecipients (search) {
    return new Promise((resolve, reject) => {
      Vue.http.get(baseUrl + `/workgroup/${store.state.workgroupId}/destination/page/1?name=['like', '%${search}%']`).then((response) => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  }
}

export default selectService



