<template>
  <b-container fluid>
    <b-row align-v="center" class="ml-1 mt-4 mb-4">
      <h4 class="text-success">Plantillas</h4>
      <div class="v-divider"></div>
      <b-col>
        <router-link :to="{name: 'dashboard'}"><i class="fas fa-home text-muted mr-2 link--top"></i></router-link>-
        <a class="ml-2">Plantillas</a>
      </b-col>
    </b-row>
    <b-row>
      <b-col md="6" class="my-1">
        <b-form-fieldset label-text-align="left" horizontal label="Filtrar: " :label-cols="1">
          <b-input-group>
            <b-form-input v-model="filter" placeholder="Buscar por..." />
              <b-input-group-append>
                <b-btn :disabled="!filter" @click="filter = ''">Limpiar</b-btn>
              </b-input-group-append>
          </b-input-group>
        </b-form-fieldset>
      </b-col>
      <b-col align-self="center" class="text-right mb-3">
        <b-button v-if="checkedItems.length > 0" size="sm" @click="this.$refs.deleteTemplate.show" variant="danger">Eliminar registro(s)</b-button>
        <b-button size="sm" @click="newRecord" variant="primary">Nuevo registro</b-button>
      </b-col>
    </b-row>

    <b-table id="TBTemplates" hover show-empty head-variant="inverse"
            empty-text="No hay registros para mostrar"
            empty-filtered-text="No hay registros que coincidan con su solicitud"
            stacked="md"
            v-model="tableValues"
            :busy="isBusy"
            :items="myProvider"
            :fields="fields"
            :filter="filter"
            :current-page="currentPage"
            :per-page="perPage"
            @head-clicked="clearSelected"
            @row-clicked="editRecord">
    <template slot="HEAD_actions" slot-scope="head">
      <input type="checkbox" @click.stop="toggleSelected" v-model="allSelected">
    </template>
    <template slot="actions" slot-scope="item">
      <input class="checkboxtext" type="checkbox" name="checked" :key="item.index" :value="item.item" @click.stop v-model="checkedItems">
    </template>

    <template slot="name" slot-scope="item">
        <span class="d-inline-block text-truncate text-adjust-md">
          {{ item.value }}
        </span>
    </template>
    <template slot="body" slot-scope="item">
        <span class="d-inline-block text-truncate text-adjust-lg">
          {{ item.value }}
        </span>
    </template>
    <template slot="date_created" slot-scope="item">{{ item.value | moment("DD-MM-YYYY") }}</template>
  </b-table>

  <b-pagination v-if="!isBusy"
                size="md"
                 align="center"
                 :total-rows="totalRows"
                 :per-page="perPage"
                 v-model="currentPage">
  </b-pagination>
  <div v-else class="text-center my-2">Cargando Datos...</div>

    <modal-template></modal-template>

    <b-modal centered ref="deleteTemplate" hide-footer title="Eliminar Registro(s)">
      <div class="d-block text-center mt-1 mb-4">
        <h5>¿Desea eliminar los registros seleccionados?</h5>
      </div>
      <b-row align-h="end">
        <b-button size="sm" class="mr-2 w-25" variant="secondary" @click="onHide">Cancelar</b-button>
        <b-button size="sm" class="mr-2 w-25" variant="primary" @click="deleteRecord">Ok</b-button>
      </b-row>
    </b-modal>

  </b-container>
</template>

<script>
import templateService from '../../services/template.service'
import modalTemplate from './editTemplate'

export default {
  components: {
    'modal-template': modalTemplate
  },
  computed: {
  },
  data () {
    return {
      items: [],
      isBusy: false,
      currentPage: 1,
      perPage: 10,
      totalRows: 0,
      filter: null,
      checkedItems: [],
      tableValues: [],
      allSelected: false,
      sortField: '',
      fields: [
        { key: 'actions', label: 'Seleccionar', class: 'text-center' },
        { key: 'name', label: 'Nombre', sortable: true, class: 'text-left' },
        { key: 'body', label: 'Contenido', sortable: true, class: 'text-left' },
        { key: 'date_created', label: 'Creación', sortable: true, class: 'text-center' }
      ]
    }
  },
  watch: {
    checkedItems (a, b) {
      if (this.tableValues.length === a.length) {
        this.allSelected = true
      } else {
        this.allSelected = false
      }
    },
    perPage (a, b) {
      if (a !== b) {
        this.clearSelected()
      }
    },
    currentPage (a, b) {
      if (a !== b) {
        this.clearSelected()
      }
    },
    filter (a, b) {
      if (a !== b) {
        this.clearSelected()
      }
    },
    async (a, b) {
      if (a !== b) {
        this.clearSelected()
      }
    }
  },
  methods: {

    myProvider (ctx) {
      this.isBusy = true
      this.sortField = ''

      if (ctx.sortBy) {
        this.sortField = ctx.sortDesc ? '?orderbydesc=' + ctx.sortBy : '?orderby=' + ctx.sortBy
      }

      let promise = templateService.getAllTemplates(ctx.currentPage, ctx.sortBy, ctx.sortDesc, ctx.filter)

      return promise.then((data) => {
        const items = data.body.items
        this.totalRows = parseInt(data.body.total)
        this.isBusy = false
        return (items)
      }).catch(data => {
        this.isBusy = false
        return []
      })
    },

    toggleSelected () {
      this.allSelected = !this.allSelected
      this.checkedItems = this.allSelected ? this.tableValues : []
    },
    clearSelected () {
      this.allSelected = false
      this.checkedItems = []
    },
    editRecord (record, index, e) {
      this.$store.dispatch('setLoading', true)
      this.$store.dispatch('template/setTemplateId', record.id).then(() => {
        this.$store.dispatch('setLoading', false)
        this.$root.$emit('bv::show::modal', 'modalTemplate')
      }).catch(() => { this.$store.dispatch('setLoading', false) })
    },

    newRecord () {
      this.$root.$emit('bv::show::modal', 'modalTemplate')
    },

    onHide () {
      this.$refs.deleteTemplate.hide()
    },

    refresh () {
      this.clearSelected()
      this.$root.$emit('bv::refresh::table', 'TBTemplates')
    },

    deleteRecord () {
      this.$store.dispatch('setLoading', true)
      this.$store.dispatch('template/deleteTemplate', this.checkedItems).then(() => {
        this.refresh()
        this.onHide()
        this.$store.dispatch('setLoading', false)
        this.$toaster.success('Registro(s) eliminado(s) exitosamente.')
      }).catch(data => {
        this.$store.dispatch('setLoading', false)
        this.$toaster.error('Error: ' + data.body.message)
      })
    }
  }
}
</script>



