<template>
  <b-container fluid>
    <b-row align-v="center" class="ml-1 mt-4 mb-4">
      <h4 class="text-success">Contactos</h4>
      <div class="v-divider"></div>
      <b-col>
        <router-link :to="{name: 'dashboard'}"><i class="fas fa-home text-muted mr-2 link--top"></i></router-link>-
        <router-link :to="{name: 'contacts'}" class="link--top"><a class="ml-2 text-muted mr-2 link--top">Contactos</a></router-link>-
        <a class="ml-2 text-muted">{{ id === 0 ? 'Nuevo contacto' : 'Editar contacto'}}</a>
      </b-col>
      <b-col v-if="id != 0" class="text-right mt-0">
        <b-button size="sm" class="mr-2" @click="onShow" variant="danger">Eliminar resgistro</b-button>
        <b-button size="sm" class="mr-2" @click="onReset" variant="primary" >Nuevo registro</b-button>
      </b-col>
    </b-row>
  <div>
    <b-form @submit="onSubmit">
      <b-card-group deck>
      <b-card class="card" title="1. Datos Personales">
       <b-container>
        <div class="align-right">
          <b-img :src="require('../../../assets/img/profiledummy.png')" alt="Responsive image"/>
        </div>
        <div class="align-left">
          <b-form-group
                  id="groupName"
                  label="Nombres:"
                  label-class="text-sm-right"
                  label-for="innombre">
            <b-form-input type="text" ref="focusThis" required v-model="name" id="innombre"></b-form-input>
          </b-form-group>
          <b-form-group
                  label="Apellidos:"
                  label-class="text-sm-right"
                  label-for="inapellido">
            <b-form-input type="text" v-model="lastname" id="inapellido"></b-form-input>
          </b-form-group>
         </div>
       </b-container>
          <b-form-group
                  label="Genero:"
                  label-class="text-sm-right"
                  label-for="ingenero">
              <b-form-radio-group class="mt-2" v-model="gender" id="ingenero" name="selectGenero">
                <b-form-radio value="F">Femenino</b-form-radio>
                <b-form-radio value="M">Masculino</b-form-radio>
              </b-form-radio-group>
          </b-form-group>
          <b-form-group
                  label="Fecha Nacimiento:"
                  label-class="text-sm-right"
                  label-for="innacimiento">
            <date-picker type="date" id="innacimiento" v-model="birthday" format="dd-MM-yyyy" editable lang="es" :first-day-of-week="1"></date-picker>
            <!-- <b-form-input size="sm" v-model="form.birthday" type="date" id="innacimiento"></b-form-input> -->
          </b-form-group>
          <b-form-group
                  label="Telefono:"
                  label-class="text-sm-right"
                  label-for="inTelefono">
                  <v-select multiple taggable push-tags noDrop :closeOnSelect="false"
                    placeholder="Numeros telefonicos..."
                    id="numbersEdit"
                    label="number"
                    v-model="selectNumbers"
                    :options="numbers">
                  <span slot="no-options">No hay opciones que coincidan!</span>
                </v-select>
          </b-form-group>
          <b-form-group
                  label="Email:"
                  label-class="text-sm-right"
                  label-for="inEmail">
            <b-form-input v-model="email" type="email" id="inEmail"></b-form-input>
          </b-form-group>
      </b-card>

      <b-card title="2. Dirección" class="ml-0">
          <b-form-group
                  label="Calle:"
                  label-class="text-sm-right"
                  label-for="inCalle">
            <b-form-input type="text" v-model="street" id="inCalle" placeholder="Calle, Avenida, Apartamento, Edificio, Piso, Etc..."></b-form-input>
          </b-form-group>
          <b-form-group
                  label="Cuidad:"
                  label-class="text-sm-right"
                  label-for="inCiudad">
            <b-form-input type="text" v-model="city" id="inCiudad"></b-form-input>
          </b-form-group>
          <b-form-group
                  label="Estado:"
                  label-class="text-sm-right"
                  label-for="inEstado">
            <b-form-input type="text" v-model="state" id="inEstado"></b-form-input>
          </b-form-group>
          <b-form-group
                  label="Pais:"
                  label-class="text-sm-right"
                  label-for="inPais">
            <b-form-input type="text" v-model="country" id="inPais"></b-form-input>
          </b-form-group>
          <b-form-group
                  label="Codigo Postal:"
                  label-class="text-sm-right"
                  label-for="inCodigo">
            <b-form-input type="text" v-model="zip" id="inCodigo"></b-form-input>
          </b-form-group>
      </b-card>

      <b-card title="3. Grupos" class="ml-0">
      </b-card>
      </b-card-group>

      <b-row align-h="end" class="mt-3 mr-1">
        <b-button class="mr-2" type="button" v-on:click="onCancel" variant="dark">Cancelar</b-button>
        <b-button class="mr-0" type="submit" variant="primary">Aceptar</b-button>
      </b-row>
    </b-form>
    </div>

    <b-modal centered ref="deleteThisContact" hide-footer title="Eliminar Registro">
      <div class="d-block text-center mt-1 mb-4">
        <h5>¿Desea eliminar el registro actual?</h5>
      </div>
      <b-row align-h="end">
        <b-button size="sm" class="mr-2 w-25" variant="secondary" @click="onHide">Cancelar</b-button>
        <b-button size="sm" class="mr-2 w-25" variant="primary" @click="onDelete">Ok</b-button>
      </b-row>
    </b-modal>
  </b-container>
</template>

<script>
import { mapGetters } from 'vuex'
import DatePicker from 'vue2-datepicker'
import select from 'vue-select'

export default {
  components: {
    DatePicker,
    'v-select': select
  },
  computed: {
    ...mapGetters({
      id: 'contacts/getContactById'
    })
  },
  data () {
    return {
      name: '',
      lastname: '',
      gender: '',
      email: '',
      birthday: '',
      numbers: [],
      country: '',
      state: '',
      city: '',
      street: '',
      zip: '',
      selectNumbers: [],
      idAddress: 0
    }
  },
  methods: {
    onShow () {
      this.$refs.deleteThisContact.show()
    },

    onHide () {
      this.$refs.deleteThisContact.hide()
    },

    onDelete () {
      this.$store.dispatch('setLoading', true)
      var array = []
      this.$set(array, 0, {'id': this.id})
      this.$store.dispatch('contacts/deleteContact', array).then(() => {
        this.onCancel()
        this.$toaster.success('Registro eliminado exitosamente.')
        this.$store.dispatch('setLoading', false)
      }).catch(data => {
        this.$store.dispatch('setLoading', false)
        this.$toaster.error('Error: ' + data.body.message)
      })
    },

    onCancel () {
      this.$router.go(-1)
    },

    onReset () {
      this.$store.commit('contacts/setContactId', 0)
      this.name = ''
      this.lastname = ''
      this.gender = ''
      this.email = ''
      this.birthday = ''
      this.numbers = []
      this.country = ''
      this.state = ''
      this.city = ''
      this.street = ''
      this.zip = ''
      this.selectNumbers = []
      this.idAddress = 0
      this.$refs.focusThis.focus()
    },

    getData () {
      const contact = this.$store.state.contacts.contactItem
      this.name = contact.name
      this.lastname = contact.lastname
      this.gender = contact.gender
      this.email = contact.email
      if (contact.birthday) {
        this.birthday = new Date(this.$moment(contact.birthday, 'DD-MM-YYYY').format())
      }

      if ((contact.address) && (contact.address.length > 0)) {
        this.idAddress = contact.address[0].id
        this.country = contact.address[0].country
        this.state = contact.address[0].state
        this.city = contact.address[0].city
        this.street = contact.address[0].street
        this.zip = contact.address[0].zip
      }

      this.selectNumbers = []
      if ((contact.numbers) && (contact.numbers.length > 0)) {
        this.numbers = contact.numbers
        for (var i = 0; i < this.numbers.length; i++) {
          this.$set(this.selectNumbers, i, this.numbers[i].number)
        }
      }
    },

    checkIsNumberExist: function (arrayP, number) {
      var index = -1
      for (var i = 0; i < arrayP.length; i++) {
        if (arrayP[i].number === number) {
          index = i
          break
        }
      }
      return index
    },

    formPrepare: function () {
      var form = {}
      var anumbers = []
      var address = {}
      var numbertxt = ''

      if (this.id !== 0) {
        this.$set(form, 'id', this.id)
      }
      this.$set(form, 'name', this.name)
      this.$set(form, 'lastname', this.lastname)
      this.$set(form, 'email', this.email)
      this.$set(form, 'gender', this.gender)

      if (this.birthday !== '') {
        this.$set(form, 'birthday', this.$moment(this.birthday, 'DD-MM-YYYY').format('YYYY-MM-DD'))
      }

      this.$set(address, 'country', this.country)
      this.$set(address, 'state', this.state)
      this.$set(address, 'city', this.city)
      this.$set(address, 'street', this.street)
      this.$set(address, 'zip', this.zip)

      if (this.idAddress !== 0) {
        this.$set(address, 'id', this.idAddress)
      }

      this.$set(form, 'address', [address])
      console.log(this.selectNumbers)
      for (var i = 0; i < this.selectNumbers.length; i++) {
        if (typeof this.selectNumbers[i] === 'object') {
          numbertxt = this.selectNumbers[i].number
        } else {
          numbertxt = this.selectNumbers[i]
        }

        var index = this.checkIsNumberExist(this.numbers, numbertxt)
        if (index === -1) {
          this.$set(anumbers, i, {'type': '0', 'number': numbertxt})
        } else {
          this.$set(anumbers, i, {'type': '0', 'number': this.numbers[i].number, 'id': this.numbers[index].id})
        }
      }
      this.$set(form, 'numbers', anumbers)
      return JSON.stringify(form)
    },

    newRecord () {
      const JsonData = this.formPrepare()
      let promise = this.$store.dispatch('contacts/newContact', JsonData)

      promise.then(response => {
        this.$toaster.success('Operación exitosa.')
        this.$store.dispatch('contacts/setContactId', response.data.id).then(() => {
          this.getData()
        })

        this.$store.dispatch('setLoading', false)
      }).catch(() => { this.$store.dispatch('setLoading', false) })
    },

    updateRecord () {
      const JsonData = this.formPrepare()
      let promise = this.$store.dispatch('contacts/updateContact', JsonData)

      promise.then(data => {
        this.$toaster.success('Operación exitosa.')
        this.$store.dispatch('contacts/getContactById', this.id).then(() => {
          this.getData()
        })
        this.$store.dispatch('setLoading', false)
      }).catch(() => { this.$store.dispatch('setLoading', false) })
    },

    onSubmit (evt) {
      evt.preventDefault()
      if (this.selectNumbers.length > 0) {
        this.$store.dispatch('setLoading', true)
        if (this.id !== 0) {
          this.updateRecord()
        } else {
          this.newRecord()
        }
      } else {
        this.$toaster.warning('Debe ingresar al menos un número telefonico.')
      }
    }
  },
  beforeMount () {
    if (this.id !== 0) {
      this.getData()
    }
  }
}
</script>



