import Vue from 'vue'
import VueResource from 'vue-resource'
import store from '../vuex/index'

Vue.use(VueResource)

const baseUrl = require('./config.js').ConectionUrlBase

const workgroupsService = {
  getWorkgroups () {
    return new Promise((resolve, reject) => {
      Vue.http.get(baseUrl + '/workgroup').then(response => {
        resolve(response.data)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  }
}

export default workgroupsService



