<template>
  <b-container fluid>
    <b-row class="container-h">
      <b-col class="d-none d-sm-block" align-self="start" style="background-color: #f0f0f0">
        <b-row align-h="center">
          <img class="logo--presentation" :src="require('../../assets/img/common/logo_complete_parrot_rgb.svg')" alt="parrot">
        </b-row>
        <b-row>
          <div class="container-decorated"></div>
        </b-row>
      </b-col>
      <b-col class="container--theme-green" align-self="center">
        <b-row align-h="center">
          <img class="logo--mobile d-block d-sm-none" src="../../assets/img/common/logo_bw_parrot_rgb.svg" alt="parrot">
        </b-row>
        <b-row align-h="center">
          <div class="block text-center">
            <h2 class="md-headline heading--light">Bienvenido</h2>

            <div class="text-left box-shadow">
              <b-form @submit="onSubmit" class="form__login">
                <b-form-group id="emailgroup">
                  <b-form-input id="emailtxt"
                                class="mt-5"
                                type="email"
                                v-model="form.email"
                                required
                                placeholder="Email *"
                                ref="focusEmail">
                  </b-form-input>
                </b-form-group>
                <b-form-group id="passwordgroup">
                  <b-form-input id="passwordtxt"
                                class="mt-2"
                                type="password"
                                v-model="form.password"
                                required
                                placeholder="Password *">
                  </b-form-input>
                </b-form-group>
                <b-button type="submit" class="w-100 mt-2 mb-3" variant="primary">Entrar</b-button>
                <b-alert variant="danger" dismissible :show="showDismissibleAlert" @dismissed="showDismissibleAlert=false">
                  Datos incorrectos!
                </b-alert>
              </b-form>
              <div class="form__footer text-center">
                <div class="py-3"><a ui-sref="#" class="link--light">¿Olvidaste tú contraseña?</a></div>
              </div>
            </div>

            <div class="mt-2">
              <span class="text--dark">¿No tienes cuenta? </span>
              <a @click="$root.$emit('bv::show::modal', 'modalSignup')" class="link--light"><strong>Registrate</strong></a>
            </div>
          </div>
        </b-row>
      </b-col>
    </b-row>
    <modal-signup></modal-signup>
  </b-container>
</template>

<script>
import { mapGetters } from 'vuex'
import signup from '../signup/signup'

export default {
  components: {
    'modal-signup': signup
  },
  computed: {
    ...mapGetters({
      isLoading: 'getLoading'
    })
  },
  data () {
    return {
      form: {
        email: '',
        password: ''
      },
      showDismissibleAlert: false
    }
  },
  methods: {
    /**
    * Delete an array of message
    * @param  {event} evt click event to invocate
    * @return {event}    process is succesfull.
    */
    onSubmit (evt) {
      evt.preventDefault()
      this.showDismissibleAlert = false
      this.$store.dispatch('setLoading', true)
      this.$store.dispatch('login', this.form).then(() => {
        this.$store.dispatch('setLoading', false)
      }).catch((data) => {
        this.$store.dispatch('setLoading', false)
        if ((data.body) && (data.body.code === 0)) {
          this.showDismissibleAlert = true
        } else {
          this.$toaster.error('Ha ocurrido un error. Error: ' + data.message)
        }
      })
    }
  }

}
</script>



