<template>
  <div id="app">
    <router-view></router-view>
    <loading v-if="isLoading"></loading>
  </div>
</template>

<script>
import loading from './components/shared/loading'
import { mapGetters } from 'vuex'

export default {
  name: 'ParrotMS',
  components: {
    'loading': loading
  },
  computed: {
    ...mapGetters({
      isLoading: 'getLoading'
    })
  }
}
</script>



