<template>
  <div>
    <span>Editar Grupo Contactos</span>
    <b-form>

      <b-container>
        <b-row align-h="end">
          <b-button class="mr-2" type="button" v-on:click="onCancel" variant="dark">Cancelar</b-button>
          <b-button class="mr-0" type="button" v-on:click="onAccept" variant="primary">Aceptar</b-button>
        </b-row>
      </b-container>
    </b-form>
  </div>
</template>

<script>
export default {
  data () {
    return {

    }
  },

  methods: {
    onAccept () {
      alert('probando')
    },

    onCancel () {
      this.$router.go(-1)
    }
  }
}
</script>



