import signupService from '../services/singup.service'

export default {
  namespaced: true,
  state: {},

  getters: {},

  mutations: {},

  actions: {
    verifyOwnerExistence (context, workgroup) {
      return new Promise((resolve, reject) => {
        signupService.verifyOwnerExistence(workgroup).then((response) => {
          if (response.body.code === '004') {
            resolve(true)
          } else {
            resolve(false)
          }
        }).catch(response => {
          console.error('Fallo al verificar existencia de workgroup -> ERROR: ', response)
          reject(response)
        })
      })
    },

    registerOwner (context, JSONData) {
      return new Promise((resolve, reject) => {
        signupService.registerOwner(JSONData).then((response) => {
          resolve(response)
        }).catch(response => {
          reject(response)
        })
      })
    }
  }
}



