<template>
  <b-modal @ok="handleOk" centered size="md"
          ref="modalSignup"
          id="modalSignup"
          title="Crear Perfil"
          no-close-on-backdrop
          hide-footer
          :header-bg-variant="headerBgVariant"
          :header-text-variant="headerTextVariant"
          @shown="focusElement"
          @hidden="reset">
    <b-form class="mr-3 ml-3" @submit.stop.prevent="onSubmit">
      <b-form-group
            id="groupName"
            label="Nombre del grupo:"
            label-class="text-sm-right"
            label-for="txtgroupName">
        <b-form-input type="text" placeholder="Introduzca un nombre de grupo *" ref="focusWorkgroup" required v-model="form.workgroup" id="txtgroupName"></b-form-input>
      </b-form-group>
      <b-form-group
            id="email"
            label="Email:"
            label-class="text-sm-right"
            label-for="txtemail">
        <b-form-input type="email" placeholder="Introduzca un email *" ref="focusEmail" required v-model="form.email" id="txtemail"></b-form-input>
      </b-form-group>
      <b-form-group
            id="password"
            label="Password:"
            label-class="text-sm-right"
            label-for="txtpassword">
        <b-form-input type="password" placeholder="Introduzca su password *" ref="focusPassword" required v-model="form.password" id="txtpassword"></b-form-input>
      </b-form-group>
      <b-form-group
            id="rptpassword"
            label="Repetir password:"
            label-class="text-sm-right"
            label-for="rtpassword">
        <b-form-input type="password" placeholder="Repita su password *" required v-model="rpassword" id="rtpassword"></b-form-input>
      </b-form-group>

      <b-container>
        <b-row align-h="end">
          <b-button class="mr-2" type="button" v-on:click="onCancel" variant="dark">Cancelar</b-button>
          <b-button class="mr-0" type="submit" variant="primary">Aceptar</b-button>
        </b-row>
      </b-container>
    </b-form>
  </b-modal>
</template>

<script>
export default {
  data () {
    return {
      headerBgVariant: 'success',
      headerTextVariant: 'light',
      form: {
        workgroup: '',
        email: '',
        password: '',
        name: '',
        phone: ''
      },
      rpassword: ''
    }
  },
  methods: {
    handleOk (evt) {
      evt.preventDefault()
    },

    reset () {
      this.form.workgroup = ''
      this.form.email = ''
      this.form.name = ''
      this.form.password = ''
      this.rpassword = ''
    },

    focusElement (e) {
      this.$refs.focusWorkgroup.focus()
    },

    formPrepare () {
      this.form.name = this.form.email
      this.form.phone = ''
      return JSON.stringify(this.form)
    },

    saveUser () {
      const JsonData = this.formPrepare()
      let promise = this.$store.dispatch('signup/registerOwner', JsonData)
      console.info(JsonData)
      promise.then(response => {
        this.$root.$emit('bv::hide::modal', 'modalSignup')
        this.$store.dispatch('setLoading', false)
        this.$toaster.success('¡Usuario registrado!')
      }).catch((response) => {
        if (response.body.code === 10) {
          this.$toaster.warning('¡Email ya se encuentra registrado!')
          this.$refs.focusEmail.focus()
        } else {
          this.$toaster.error('Ha ocurrido un error, detalle: ' + response.body.message)
        }
        this.$store.dispatch('setLoading', false)
      })
    },

    onSubmit () {
      this.$store.dispatch('setLoading', true)
      let promise = this.$store.dispatch('signup/verifyOwnerExistence', this.form.workgroup)

      promise.then((isExist) => {
        if (!isExist) {
          if (this.form.password === this.rpassword) {
            this.saveUser()
          } else {
            this.$store.dispatch('setLoading', false)
            this.$toaster.warning('¡La contraseña no coincide!')
            this.$refs.focusPassword.focus()
          }
        } else {
          this.$store.dispatch('setLoading', false)
          this.$toaster.warning('¡Nombre de grupo en uso!')
          this.$refs.focusWorkgroup.focus()
        }
      }).catch(() => { this.$store.dispatch('setLoading', false) })
    },
    onCancel () {
      this.$root.$emit('bv::hide::modal', 'modalSignup')
    }
  }
}
</script>



