import contactService from '../services/contacts.service'

export default {
  namespaced: true,
  state: {
    contactId: 0,
    contactItem: []
  },

  getters: {
    getContactById: (state) => {
      return state.contactId
    }
  },

  mutations: {
    setContactId (state, id) {
      if (id !== state.contactId) {
        state.contactId = id
        state.contactItem = []
      }
    },

    setConversationItem (state, data) {
      state.contactItem = data
    }
  },

  actions: {
    /**
    * Acciones para contactos
    */
    setContactId (context, id) {
      context.commit('setContactId', id)
      return new Promise((resolve, reject) => {
        context.dispatch('getContactById').then((response) => {
          resolve(response)
        }).catch(response => {
          reject(response)
        })
      })
    },

    getContactById (context) {
      return new Promise((resolve, reject) => {
        contactService.getContactById(context.state.contactId).then((response) => {
          context.commit('setConversationItem', response.data)
          resolve(response.data)
        }).catch(response => {
          reject(response)
        })
      })
    },

    newContact (context, JSonData) {
      return new Promise((resolve, reject) => {
        contactService.newContact(JSonData).then((response) => {
          // context.dispatch('setContactId', response.data.id)
          resolve(response)
        }).catch(response => {
          reject(response)
        })
      })
    },

    updateContact (context, JSonData) {
      return new Promise((resolve, reject) => {
        contactService.updateContact(JSonData).then((response) => {
          context.commit('setContactId', response.data.id)
          resolve(response)
        }).catch(response => {
          reject(response)
        })
      })
    },

    deleteContact (context, record) {
      return new Promise((resolve, reject) => {
        contactService.deleteContact(record)
          .then((data) => {
            resolve()
          }).catch(data => {
            console.error('Fallo eliminar contacto, ERROR: ' + data.body.message)
            reject(data)
          })
      })
    },

    /**
    * Acciones para grupos de contactos
    */

    deleteGroup (context, record) {
      return new Promise((resolve, reject) => {
        contactService.deleteGroup(record)
          .then((response) => {
            resolve(response)
          }).catch(response => {
            console.error('Fallo eliminar grupo, ERROR: ' + response.body.message)
            reject(response)
          })
      })
    }
  }
}



