import Vue from 'vue'
import VueResource from 'vue-resource'
import store from '../vuex/index'
Vue.use(VueResource)

const baseUrl = require('./config.js').ConectionUrlBase
var sortField = ''
var filterField = ''

const messagesService = {
  getAllConversation (page, orderBy, orderDesc, filter) {
    sortField = '?orderbydesc=date_write'
    filterField = ''
    if (orderBy) {
      sortField = orderDesc ? '?orderbydesc=' + orderBy : '?orderby=' + orderBy
    }
    if (filter) {
      filterField = '?content=[\'like\',\'%' + filter + '%\']'
    }

    return new Promise((resolve, reject) => {
      Vue.http.get(baseUrl + '/message/page/' + page + sortField + filterField).then((response) => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  getConversationById (id) {
    return new Promise((resolve, reject) => {
      Vue.http.get(baseUrl + `/message/${id}`).then((response) => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  setConversation (json) {
    return new Promise((resolve, reject) => {
      Vue.http.post(baseUrl + '/message', json).then((response) => {
        resolve(response.data)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  deleteConversation (record) {
    return new Promise((resolve, reject) => {
      for (var i = 0; i < record.length; i++) {
        Vue.http.delete(baseUrl + '/message/' + record[i].id).then(response => {
          resolve(response.data)
        }).catch(response => {
          store.dispatch('globalErrorHandler', response)
          reject(response)
        })
      }
    })
  }
}

export default messagesService



