import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router'
import Toaster from 'v-toaster'

import messages from './messages.vuex.js'
import template from './template.vuex.js'
import contacts from './contacts.vuex.js'
import signup from './signup.vuex.js'

import loginService from '../services/login.service.js'
import workgroupService from '../services/workgroup.service.js'

Vue.use(Vuex)
Vue.use(Toaster, {timeout: 5000})

const state = {
  isAuthenticated: false,
  workgroups: [],
  workgroupId: 0,
  isLoading: false
}

const store = new Vuex.Store({
  modules: {
    messages,
    template,
    contacts,
    signup
  },
  state,
  getters: {
    isAuthenticated: (state) => {
      return state.isAuthenticated
    },

    getWorkgroupId: (state) => {
      return state.workgroupId
    },

    getLoading: (state) => {
      return state.isLoading
    }

  },
  actions: {
    setLoading (context, value) {
      context.commit('setLoading', value)
    },

    changeWorkgroup (context, newId) {
      context.commit('changeWorkgroup', newId)
    },

    login (context, form) {
      return new Promise((resolve, reject) => {
        loginService.getAuthetication(form)
          .then((data) => {
            context.commit('login', { user: form.email, token: data.token })
            resolve()
          }).catch(data => {
            console.error('Fallo iniciar session -> ERROR: ' + data.message)
            reject(data)
          })
      })
    },

    logout (context) {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          context.commit('logout')
        }, 1500)
      })
    },
    globalErrorHandler (context, error) {
      var isError = false
      if (error.body.message) {
        if (error.body.message === 'Expired token') {
          console.error('Sesion vencida, inicie una nueva sesión')
          isError = true
        } else if (error.body.message === 'Broken authorization: null') {
          console.error('Sesion vencida, inicie una nueva sesión')
          isError = true
        } else {
          Vue.toaster.error('Ha ocurrido un error, detalle: ' + error.body.message)
        }
        if (isError) {
          router.push('/login')
        }
      } else {
        Vue.toaster.error('ERROR: No es posible establecer comunicación con el servidor.')
      }
    },

    /**
    * Realiza peticion al servicio de workgroup para obtener el listado.
    **/
    getWorkgroups (context) {
      return new Promise((resolve, reject) => {
        workgroupService.getWorkgroups()
          .then((data) => {
            context.commit('getWorkgroups', data.items)
            resolve()
          }).catch(data => {
            console.error('Fallo cargar workgroup -> ERROR: ', data)
            reject(data)
          })
      })
    }
  },
  mutations: {
    setLoading (state, value) {
      state.isLoading = value
    },

    changeWorkgroup (state, id) {
      state.workgroupId = id
    },

    login (state, data) {
      state.isAuthenticated = true
      window.localStorage.setItem('username', data.user)
      window.localStorage.setItem('token', data.token)
      state.isAuthenticated = true
      router.push({'name': 'dashboard'})
    },

    logout (state) {
      state.isAuthenticated = false
      window.localStorage.setItem('username', null)
      window.localStorage.setItem('token', null)
      state.isAuthenticated = false
      state.isLoading = false
      router.push({'name': 'login'})
    },

    getWorkgroups (state, data) {
      state.workgroups = data
    }
  }
})

export default store



