import Vue from 'vue'
import Router from 'vue-router'
import store from '../vuex/index'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  linkActiveClass: 'is-active',
  scrollBehavior: (to, from, savedPosition) => ({ y: 0 }),
  routes: [
    {
      path: '/login',
      name: 'login',
      component: require('@/components/login/login').default
    },
    {
      path: '/',
      name: 'ParrotMS',
      component: require('@/components/appLayout').default,
      meta: { requiresAuth: true },
      children: [
        {
          path: 'dashboard',
          name: 'dashboard',
          component: require('@/components/dashboard').default
        },
        {
          path: 'conversation',
          name: 'conversation',
          component: require('@/components/messages/listConversation').default
        },
        {
          path: 'scheduled',
          component: require('@/components/messages/listScheduled').default
        },
        {
          path: 'contacts_all',
          component: require('@/components/contacts/all/listAll').default
        },
        {
          path: 'group',
          component: require('@/components/contacts/group/listGroup').default
        },
        {
          path: 'group_edit',
          component: require('@/components/contacts/group/editGroup').default
        },
        {
          path: 'contacts',
          name: 'contacts',
          component: require('@/components/contacts/individual/listIndividual').default
        },
        {
          path: 'contacts_edit',
          name: 'contacts_edit',
          component: require('@/components/contacts/individual/editIndividual').default
        },
        {
          path: 'statistics',
          component: require('@/components/statistics').default
        },
        {
          path: 'users',
          component: require('@/components/users/listUsers').default
        },
        {
          path: 'events',
          component: require('@/components/events').default
        },
        {
          path: 'templates',
          name: 'templates',
          component: require('@/components/template/listTemplate').default
        }
      ]
    },
    {
      path: '*',
      name: 'Not Found',
      component: require('@/components/NotFound').default
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!store.state.isAuthenticated) {
      next({
        path: '/login'
      })
    } else {
      next()
    }
  } else {
    next()
  }
})

export default router



