<template>
  <b-modal @ok="handleOk" centered size="lg"
          ref="modalConversation"
          id="modalConversation"
          :title="id !== 0 ? 'Reenviar mensaje' : 'Nuevo mensaje'"
          no-close-on-backdrop
          lazy
          hide-footer
          :header-bg-variant="headerBgVariant"
          :header-text-variant="headerTextVariant"
          @show="getData"
          @hidden="reset">
    <b-form @submit.stop.prevent="handleSubmit">
      <b-row>
        <b-col>
          <b-form-group id="lbdestinatario"
                        label="Destinatario(s)"
                        label-for="search">
            <v-select multiple
                      placeholder="Buscar destinatario(s)..."
                      id="search"
                      v-model="recipients"
                      label="name"
                      @search="search"
                      :options="options" :closeOnSelect="false">
              <span slot="no-options">Lo sentimos, no hay opciones que coincidan!</span>
            </v-select>
          </b-form-group>
        </b-col>
      </b-row>

      <b-row>
        <b-col>
          <b-form-group label="Tipo de mensaje">
            <b-form-radio-group id="type" v-model="type_content" name="type">
              <b-form-radio value="0">SMS</b-form-radio>
              <b-form-radio value="1">Email</b-form-radio>
            </b-form-radio-group>
          </b-form-group>
        </b-col>
      </b-row>

      <b-row>
        <b-col>
          <b-form-group id="lbtContenido"
                        label="Contenido:"
                        label-for="txcontenido">
          <b-form-textarea id="txcontenido"
                     v-model="content"
                     ref="contenido"
                     placeholder="Introduzca el contenido del mensaje..."
                     :rows="5"
                     :max-rows="6"
                     required maxlength="160">
          </b-form-textarea>
          </b-form-group>
          <h6 class="label-count">{{ content.length + '/160'}}</h6>
        </b-col>
      </b-row>

      <b-container>
        <b-row align-h="end">
          <b-button class="mr-2" type="button" v-on:click="onCancel" variant="dark">Cancelar</b-button>
          <b-button class="mr-0" type="submit" variant="primary">Aceptar</b-button>
        </b-row>
      </b-container>
    </b-form>
  </b-modal>
</template>

<script>
import { mapGetters } from 'vuex'
import select from 'vue-select'
import selectService from '../../services/select.service'

export default {
  components: {
    'v-select': select
  },
  computed: {
    ...mapGetters({
      workgroupId: 'getWorkgroupId',
      id: 'messages/getConversationById'
    })
  },
  data () {
    return {
      headerBgVariant: 'success',
      headerTextVariant: 'light',
      options: [],
      recipients: [],
      content: '',
      type_content: ''
    }
  },
  methods: {
    search (search, loading) {
      loading(true)
      this.getRecipients(search, loading, this)
    },
    getRecipients (search, loading, vm) {
      let promise = selectService.getRecipients(search)

      return promise.then((response) => {
        vm.options = response.data.items
        loading(false)
        return (response.data)
      }).catch((response) => {
        return (response)
      })
    },
    handleOk (evt) {
      evt.preventDefault()
    },
    getData () {
      if (this.id !== 0) {
        const conversation = this.$store.state.messages.conversation
        this.content = conversation.content
        this.type_content = conversation.type_content

        /*
        if (data.destinations.length > 0) {
          for (var i = 0; i < data.destinations.length; i++) {
          this.$set(this.recipients, i, { 'id': data.destinations[i].id, 'name': data.destinations[i].address, 'type': data.destinations[i].type })
          }
        }
        */
      }
    },
    reset () {
      this.recipients = []
      this.content = ''
      this.type_content = '0'
      this.$store.commit('messages/setConversationId', 0)
    },

    formPrepare: function () {
      var form = {}
      var to = []
      this.$set(form, 'content', this.content)
      this.$set(form, 'type_content', this.type_content)
      this.$set(form, 'id_workgroup', this.workgroupId)
      this.$set(form, 'id_template', 1)

      for (var i = 0; i < this.recipients.length; i++) {
        this.$set(to, i, { 'address': this.recipients[i].id, 'type': this.recipients[i].type })
      }
      this.$set(form, 'to', to)

      return JSON.stringify(form)
    },
    handleSubmit () {
      if (this.recipients.length > 0) {
        this.$store.dispatch('setLoading', true)
        const JsonData = this.formPrepare()
        let promise = this.$store.dispatch('messages/setConversation', JsonData)

        promise.then(data => {
          this.$root.$emit('bv::refresh::table', 'TBConversation')
          this.$root.$emit('bv::hide::modal', 'modalConversation')
          this.$toaster.success('Operación exitosa.')
          this.$store.dispatch('setLoading', false)
        }).catch(() => { this.$store.dispatch('setLoading', false) })
      } else { this.$toaster.warning('Debe ingresar al menos un (1) destinatario.') }
    },
    onCancel () {
      this.$root.$emit('bv::hide::modal', 'modalConversation')
    }
  },
  mounted: function () {
  }
}
</script>



