import messagesService from '../services/messages.service'

export default {
  namespaced: true,
  state: {
    conversationId: 0,
    conversation: []
  },

  getters: {
    getConversationById: (state) => {
      return state.conversationId
    }
  },

  mutations: {
    setConversationId (state, id) {
      state.conversationId = id
      state.conversation = []
    },

    setConversationData (state, data) {
      state.conversation = data
    }
  },

  actions: {
    setConversationId (context, id) {
      context.commit('setConversationId', id)
      return new Promise((resolve, reject) => {
        context.dispatch('getConversationById').then((response) => {
          resolve(response)
        }).catch(response => {
          reject(response)
        })
      })
    },

    getConversationById (context) {
      return new Promise((resolve, reject) => {
        messagesService.getConversationById(context.state.conversationId).then((response) => {
          context.commit('setConversationData', response.data)
          resolve(response.data)
        }).catch(response => {
          reject(response)
        })
      })
    },

    setConversation (context, JSonData) {
      return new Promise((resolve, reject) => {
        messagesService.setConversation(JSonData).then((response) => {
          context.commit('setConversationData', response.data)
          resolve(response)
        }).catch(response => {
          reject(response)
        })
      })
    },

    deleteConversation (context, record) {
      return new Promise((resolve, reject) => {
        messagesService.deleteConversation(record)
          .then((data) => {
            resolve()
          }).catch(data => {
            console.error('Fallo eliminar mensaje, ERROR: ' + data.body.message)
            reject(data)
          })
      })
    },

    updateConversation (context, record) {
      return new Promise((resolve, reject) => {
        /* messagesService.updateConversation(record)
          .then((data) => {
            this.dispatch('clientes/getAllClientes')
            resolve()
          }).catch(data => {
            console.error('Fallo modificar mensaje, ERROR: ' + data.body.message)
            reject(data)
          }) */
      })
    },

    newConversation (context, record) {
      return new Promise((resolve, reject) => {
        /* messagesService.newConversation(record)
          .then((data) => {
            this.dispatch('clientes/getAllClientes')
            resolve()
          }).catch(data => {
            console.error('Fallo insertar cliente, ERROR: ' + data.body.message)
            reject(data)
          }) */
      })
    }
  }

}



