import Vue from 'vue'
import VueResource from 'vue-resource'
import store from '../vuex/index'

Vue.use(VueResource)

const baseUrl = require('./config.js').ConectionUrlBase

const singupService = {
  verifyOwnerExistence (workgroup) {
    return new Promise((resolve, reject) => {
      Vue.http.get(baseUrl + `/available/workgroup/${workgroup}`).then((response) => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  registerOwner (JSONData) {
    return new Promise((resolve, reject) => {
      Vue.http.post(baseUrl + '/suscriptor', JSONData).then((response) => {
        resolve(response)
      }).catch(response => {
        reject(response)
      })
    })
  }

}

export default singupService



