import Vue from 'vue'
import VueResource from 'vue-resource'
import store from '../vuex/index'
Vue.use(VueResource)

const baseUrl = require('./config.js').ConectionUrlBase
var sortField = ''
var filterField = ''

const usersService = {
  getAllUsers (page, orderBy, orderDesc, filter) {
    sortField = ''
    filterField = ''
    if (orderBy) {
      sortField = orderDesc ? '?orderbydesc=' + orderBy : '?orderby=' + orderBy
    }
    if (filter) {
      filterField = '?name=[\'like\',\'%' + filter + '%\']'
    }

    return new Promise((resolve, reject) => {
      Vue.http.get(baseUrl + `/workgroup/${store.state.workgroupId}/members/page/` + page + sortField + filterField).then(response => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  }
}

export default usersService



