<template>
  <div class="box-shadow">
  <b-navbar type="dark" variant="success" toggleable="md">
    <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
      <b-navbar-brand to="dashboard">ParrotMS</b-navbar-brand>

        <b-collapse is-nav id="nav_collapse">

      <b-navbar-nav>
        <b-nav-item-dropdown text="Mensaje" right>
          <b-dropdown-item to="conversation">Conversación</b-dropdown-item>
          <b-dropdown-item to="scheduled">Programados</b-dropdown-item>
        </b-nav-item-dropdown>
        <b-nav-item-dropdown text="Contacto" right>
          <b-dropdown-item to="contacts_all">Todos</b-dropdown-item>
          <b-dropdown-item to="contacts">Individuales</b-dropdown-item>
          <b-dropdown-item to="group">Grupos</b-dropdown-item>
        </b-nav-item-dropdown>
        <b-nav-item to="templates">Plantillas</b-nav-item>
        <b-nav-item to="statistics">Estadisticas</b-nav-item>
        <b-nav-item-dropdown text="Configuración" right>
          <b-dropdown-item href="#">Mi Perfil</b-dropdown-item>
          <b-dropdown-item to="users">Usuarios</b-dropdown-item>
          <b-dropdown-item href="#">Importar/Exportar</b-dropdown-item>
          <b-dropdown-item to="events">Eventos</b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav>

      <!-- alinear hacia la derecha los items del nav -->
      <b-navbar-nav class="ml-auto">
        <b-nav-item-dropdown text="Grupo de Trabajo" right>
          <b-dropdown-item v-for="(wk, index) in workgroups"
                          :key="index" v-on:click="changeWorkgroup(wk.id, wk.name)">{{ wk.name }}</b-dropdown-item>
        </b-nav-item-dropdown>
        <b-navbar-brand>{{ workgroupSelect }}</b-navbar-brand>

        <b-nav-item-dropdown text="Idioma" right>
          <b-dropdown-item href="#">ES</b-dropdown-item>
          <b-dropdown-item href="#">EN</b-dropdown-item>
          <b-dropdown-item href="#">RU</b-dropdown-item>
          <b-dropdown-item href="#">FA</b-dropdown-item>
        </b-nav-item-dropdown>

        <b-nav-item-dropdown right>
          <!-- Using button-content slot -->
          <template slot="button-content">
            <em>Usuario</em>
          </template>
          <b-dropdown-item href="#">Mi perfil</b-dropdown-item>
          <b-dropdown-item v-on:click="logout">Salir</b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  computed: {
    ...mapState({
      workgroups: state => state.workgroups
    })
  },
  data () {
    return {
      workgroupSelect: '',
      isLoading: false
    }
  },
  methods: {
    ...mapActions({
      getWorkgroups: 'getWorkgroups'
    }),
    logout () {
      this.$store.dispatch('setLoading', true)
      this.$store.dispatch('logout').then(() => {
      }).catch(() => { this.$store.dispatch('setLoading', false) })
    },
    changeWorkgroup (id, name) {
      this.workgroupSelect = name
      this.$store.dispatch('setLoading', true)
      this.$router.replace({name: 'dashboard', params: {redirect: this.$router.history.current.path}})
      setTimeout(() => {
        this.$store.dispatch('changeWorkgroup', id)
        this.$router.push({path: this.$route.params.redirect})
        this.$store.dispatch('setLoading', false)
      }, 1000)
    }
  },
  beforeMount () {
    this.$store.dispatch('setLoading', true)
    this.$nextTick(function () {
      this.getWorkgroups().then(() => {
        this.$store.dispatch('setLoading', false)
        this.workgroupSelect = this.workgroups[0].name
        this.$store.commit('changeWorkgroup', this.workgroups[0].id)
      }).catch(() => { this.$store.dispatch('setLoading', false) })
    })
  }
}
</script>



