import templateService from '../services/template.service'

export default {
  namespaced: true,
  state: {
    templateId: 0,
    template: []
  },

  getters: {
    getTemplateById: (state) => {
      return state.templateId
    }
  },

  mutations: {
    setTemplateId (state, id) {
      state.templateId = id
      state.template = []
    },

    setTemplate (state, data) {
      state.template = data
    }
  },

  actions: {
    setTemplateId (context, id) {
      context.commit('setTemplateId', id)
      return new Promise((resolve, reject) => {
        context.dispatch('getTemplateById').then((response) => {
          resolve(response)
        }).catch(response => {
          reject(response)
        })
      })
    },
    /**
     * Carga una plantilla segun un id para ser editada
     */
    getTemplateById (context) {
      return new Promise((resolve, reject) => {
        templateService.getTemplateById(context.state.templateId).then((response) => {
          context.commit('setTemplate', response.data)
          resolve(response.data)
        }).catch(response => {
          reject(response)
        })
      })
    },
    /**
     * Registra una nueva plantilla en el sistema.
     */
    setTemplate (context, JSonData) {
      return new Promise((resolve, reject) => {
        templateService.setTemplate(JSonData).then((response) => {
          resolve(response.data)
        }).catch(response => {
          reject(response)
        })
      })
    },

    updateTemplate (context, JSonData) {
      return new Promise((resolve, reject) => {
        templateService.updateTemplate(JSonData).then((response) => {
          resolve(response.data)
        }).catch(response => {
          reject(response)
        })
      })
    },

    deleteTemplate (context, record) {
      return new Promise((resolve, reject) => {
        templateService.deleteTemplate(record)
          .then((response) => {
            resolve(response.data)
          }).catch(response => {
            console.error('Fallo eliminar plantilla, ERROR: ' + response.body.message)
            reject(response)
          })
      })
    }
  }
}



