import Vue from 'vue'
import VueResource from 'vue-resource'
Vue.use(VueResource)

const baseUrl = require('./config.js').ConectionUrlBase

Vue.http.interceptors.push(function (request) {
  const token = window.localStorage.getItem('token')
  if (token) {
    request.headers.set('Authorization', 'Bearer ' + token)
  }
})

const loginService = {
  getAuthetication (form) {
    const jsonData = JSON.stringify({ username: form.email, password: form.password })
    return new Promise((resolve, reject) => {
      Vue.http.post(baseUrl + '/authorization', jsonData).then(response => {
        resolve(response.data)
      }).catch(response => {
        reject(response)
      })
    })
  }
}

export default loginService



