<template>
  <b-container fluid>
    <b-row align-v="center" class="ml-1 mt-4 mb-4">
      <h4 class="text-success">Programados</h4>
      <div class="v-divider"></div>
      <b-col>
        <router-link :to="{name: 'dashboard'}"><i class="fas fa-home text-muted mr-2 link--top"></i></router-link>-
        <a class="ml-2">Programados</a>
      </b-col>
    </b-row>
    <b-row>
      <b-col md="6" class="my-1">
        <b-form-fieldset label-text-align="left" horizontal label="Filtrar: " :label-cols="1">
          <b-input-group>
            <b-form-input v-model="filter" placeholder="Buscar por..." />
              <b-input-group-append>
                <b-btn :disabled="!filter" @click="filter = ''">Limpiar</b-btn>
              </b-input-group-append>
          </b-input-group>
        </b-form-fieldset>
      </b-col>
      <b-col align-self="center" class="text-right mb-3">
        <b-button v-if="checkedItems.length > 0" size="sm"  variant="danger">Eliminar mensaje(s)</b-button>
        <b-button size="sm" variant="primary">Nuevo mensaje</b-button>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
export default {
  data () {
    return {
      filter: null,
      checkedItems: []
    }
  },
  methods: {
    mostrar () {
      console.log(this.options)
    }
  }
}
</script>



