import Vue from 'vue'
import VueResource from 'vue-resource'
import store from '../vuex/index'
Vue.use(VueResource)

const baseUrl = require('./config.js').ConectionUrlBase
var sortField = ''
var filterField = ''

const contactsService = {
  getAllContacts (page, orderBy, orderDesc, filter) {
    sortField = ''
    filterField = ''
    if (orderBy) {
      sortField = orderDesc ? '?orderbydesc=' + orderBy : '?orderby=' + orderBy
    }
    if (filter) {
      filterField = '?name=[\'like\',\'%' + filter + '%\']'
    }

    return new Promise((resolve, reject) => {
      Vue.http.get(baseUrl + `/workgroup/${store.state.workgroupId}/contact/page/${page}` + sortField + filterField).then(response => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  getContactById (id) {
    return new Promise((resolve, reject) => {
      Vue.http.get(baseUrl + `/workgroup/${store.state.workgroupId}/contact/${id}`).then((response) => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  newContact (jsonData) {
    return new Promise((resolve, reject) => {
      Vue.http.post(baseUrl + `/workgroup/${store.state.workgroupId}/contact`, jsonData).then((response) => {
        resolve(response.data)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  updateContact (jsonData) {
    return new Promise((resolve, reject) => {
      Vue.http.put(baseUrl + `/workgroup/${store.state.workgroupId}/contact`, jsonData).then((response) => {
        resolve(response.data)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  deleteContact (record) {
    return new Promise((resolve, reject) => {
      for (var i = 0; i < record.length; i++) {
        Vue.http.delete(baseUrl + `/workgroup/${store.state.workgroupId}/contact/${record[i].id}`).then(response => {
          resolve(response.data)
        }).catch(response => {
          store.dispatch('globalErrorHandler', response)
          reject(response)
        })
      }
    })
  },

  getAllGroup (page, orderBy, orderDesc, filter) {
    sortField = ''
    filterField = ''
    if (orderBy) {
      sortField = orderDesc ? '?orderbydesc=' + orderBy : '?orderby=' + orderBy
    }
    if (filter) {
      filterField = '?name=[\'like\',\'%' + filter + '%\']'
    }

    return new Promise((resolve, reject) => {
      Vue.http.get(baseUrl + `/workgroup/${store.state.workgroupId}/group/page/${page}` + sortField + filterField).then(response => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  deleteGroup (record) {
    return new Promise((resolve, reject) => {
      for (var i = 0; i < record.length; i++) {
        Vue.http.delete(baseUrl + `/workgroup/${store.state.workgroupId}/group/${record[i].id}`).then(response => {
          resolve(response)
        }).catch(response => {
          store.dispatch('globalErrorHandler', response)
          reject(response)
        })
      }
    })
  }
}

export default contactsService



