import Vue from 'vue'
import VueResource from 'vue-resource'
import store from '../vuex/index'
Vue.use(VueResource)

const baseUrl = require('./config.js').ConectionUrlBase
var sortField = ''
var filterField = ''

const templateService = {
  getAllTemplates (page, orderBy, orderDesc, filter) {
    sortField = '?orderbydesc=date_created'
    filterField = ''
    if (orderBy) {
      sortField = orderDesc ? '?orderbydesc=' + orderBy : '?orderby=' + orderBy
    }
    if (filter) {
      filterField = '?name=[\'like\',\'%' + filter + '%\']'
    }

    return new Promise((resolve, reject) => {
      Vue.http.get(baseUrl + '/message/template/page/' + page + sortField + filterField).then(response => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  getTemplateById (id) {
    return new Promise((resolve, reject) => {
      Vue.http.get(baseUrl + `/message/template/${id}`).then((response) => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  setTemplate (json) {
    return new Promise((resolve, reject) => {
      Vue.http.post(baseUrl + '/message/template', json).then((response) => {
        resolve(response.data)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  updateTemplate (json) {
    return new Promise((resolve, reject) => {
      Vue.http.put(baseUrl + '/message/template', json).then((response) => {
        resolve(response.data)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  deleteTemplate (record) {
    return new Promise((resolve, reject) => {
      for (var i = 0; i < record.length; i++) {
        Vue.http.delete(baseUrl + `/message/template/${record[i].id}`).then(response => {
          resolve(response.data)
        }).catch(response => {
          store.dispatch('globalErrorHandler', response)
          reject(response)
        })
      }
    })
  }
}

export default templateService



